/* eslint-disable */
<template>
  <div>
    <AudioComponent @createNote="createNoteFromRecord" />
    <div class="tabs">
      <button
        v-for="tab in tabs"
        :key="tab.id"
        :class="[
          'tabs__button',
          { 'tabs__button--active': activeTab.id === tab.id },
        ]"
        @click="activeTab = tab"
      >
        {{ tab.metaName }}
      </button>
    </div>
    <div class="content">
      <div v-for="tab in tabs" :key="tab.id">
        <!-- Wyświetlanie notatek jeśli aktywna zakładka to "Notatki" -->
        <div
          style="color: red"
          v-if="activeTab.id === tab.id && tab.metaName === 'records'"
        >
          <h2>Notatki</h2>
          <ul>
            <li v-for="(note, index) in reversedNotes" :key="index">
              {{ note }}
            </li>
          </ul>
        </div>
        <!-- Reszta Twojego kodu -->
        <input
          v-if="activeTab.id === tab.id && tab.metaName !== 'records'"
          type="text"
          v-model="newTodo[tab.id]"
          @keyup.enter="addTodo(tab.id)"
          class="content__input"
        />
        <ul
          v-if="activeTab.id === tab.id && tab.metaName !== 'records'"
          class="content__list"
        >
          <li
            v-for="(todo, todoIndex) in todos[tab.id]"
            :key="todoIndex"
            class="content__list-item"
          >
            <label class="checkbox-wrapper">
              <input
                type="checkbox"
                :checked="isSelected(todo.id)"
                @change="toggleTodoSelected(todo.id)"
                class="checkbox-wrapper__input"
                :key="todo.id"
              />
              <span class="checkbox-wrapper__checkmark"></span>
              <span
                :class="[
                  'checkbox-wrapper__text',
                  { 'is-completed': isSelected(todo.id) },
                ]"
              >
                {{ todo.text }}
              </span>
            </label>
            <button
              @click="removeTodo(tab.id, todoIndex)"
              class="content__delete-button"
            >
              Usuń
            </button>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted } from "vue";
import { v4 as uuidv4 } from "uuid";
import Hammer from "hammerjs";
import axios from "axios";
// eslint-disable-next-line no-unused-vars
import AudioComponent from "./Audio.vue";

const notes = ref([]);
const hammer = ref(null);
const tabs = ref([]);
const activeTab = ref();
const todos = ref({});
const newTodo = ref({});
const selectedTodos = ref([]);

const axiosRequest = async (url, data) => {
  return await axios.post(url, {
    data,
  });
};

const getTodos = async () => {
  const req = await axiosRequest(
    "https://us-central1-superfunction-c5136.cloudfunctions.net/webApi/api/v1/functions/-NZvrDZQQepU-wM3nERQ/run"
  );
  return req.data.body;
};

const setTodos = async () => {
  const todosBase = {
    tabs: JSON.parse(JSON.stringify(tabs.value)),
    todos: JSON.parse(JSON.stringify(todos.value)),
    newTodo: JSON.parse(JSON.stringify(newTodo.value)),
    selectedTodos: JSON.parse(JSON.stringify(selectedTodos.value)),
  };
  const req = await axiosRequest(
    "https://us-central1-superfunction-c5136.cloudfunctions.net/webApi/api/v1/functions/-NZvpiTfQoYdMlz36eUh/run",
    { todosBase }
  );
  return req.data.body;
};

onMounted(async () => {
  hammer.value = new Hammer(document.body);
  hammer.value.get("swipe").set({ direction: Hammer.DIRECTION_HORIZONTAL });
  hammer.value.on("swipeleft", () => {
    const currentIndex = tabs.value.findIndex(
      (tab) => tab.id === activeTab.value.id
    );
    activeTab.value = tabs.value[(currentIndex + 1) % tabs.value.length];
  });
  hammer.value.on("swiperight", () => {
    const currentIndex = tabs.value.findIndex(
      (tab) => tab.id === activeTab.value.id
    );
    activeTab.value =
      tabs.value[(currentIndex - 1 + tabs.value.length) % tabs.value.length];
  });
  const objectFromStore = await getTodos();

  if (objectFromStore.tabs) {
    tabs.value = objectFromStore.tabs || tabs.value;
    newTodo.value = objectFromStore.newTodo || {};
    todos.value = objectFromStore.todos || {};
    selectedTodos.value = objectFromStore.selectedTodos || selectedTodos.value;
  } else {
    // Jeśli nie otrzymaliśmy żadnych danych z backendu, tworzymy domyślne dane
    tabs.value = [
      {
        id: uuidv4(),
        name: "records",
        metaName: "records",
      },
      {
        id: uuidv4(),
        name: "Add to calendar",
        metaName: "calendar",
        description:
          "Tool that adds event to calendar. If you find a mention of a date in the message then this event is responsible for it. Need text as name, and dates from and to in 'DD-MM-YYYY, HH:mm' format. Today is: 2023-07-13. Add all in polish language",
      },
      {
        id: uuidv4(),
        name: "Add to shop list",
        metaName: "shop",
        description:
          "Tool that add things to shop list. Add all what is needed to buy. If a user asks to add something needed for a recipe, for example. then add the needed ingredients to the list of theses. Return all each elemenet in after coma. Add all in polish language",
      },
      {
        id: uuidv4(),
        name: "Add to do",
        metaName: "todo",
        description:
          "Tool that add things to To Do list. Add all in polish language. Seperate each element with coma.",
      },
    ];
    newTodo.value = {};
    todos.value = {};
    selectedTodos.value = [];
  }
  activeTab.value = tabs.value[0];
  // const storedNotes = localStorage.getItem("notes");
  // if (storedNotes) {
  //   const parsedNotes = JSON.parse(storedNotes);
  //   if (!parsedNotes) return;
  //   notes.value = parsedNotes.filter((note) => typeof note === "string");
  // }
});

const addTodoText = (text, tabId = activeTab.value.id) => {
  if (text) {
    const todo = {
      id: uuidv4(),
      text: text,
    };
    if (!todos.value[tabId]) {
      todos.value[tabId] = [];
    }
    todos.value[tabId].push(todo);
    newTodo.value[tabId] = "";
  }

  setTodos();
};
// eslint-disable-next-line no-unused-vars
const reversedNotes = computed(() => {
  return notes.value.slice().reverse();
});
// eslint-disable-next-line no-unused-vars
const createNoteFromRecord = async (note) => {
  if (typeof note === !"string") return;
  notes.value.push(note);
  if (activeTab.value.name !== "records") {
    addTodoText(note);
  } else {
    const options = {
      method: "POST",
      url:
        "https://us-central1-superfunction-c5136.cloudfunctions.net/webApi/api/v1/agent",
      headers: { "Content-Type": "application/json" },
      data: {
        input: note,
        tools: tabs.value.filter((e) => e.description),
      },
    };

    axios
      .request(options)
      .then(function (response) {
        console.log(response.data);
        const tebEl = tabs.value.find((e) => e.metaName === response.data.type);
        console.log(tebEl);
        if (tebEl) {
          addTodoText(response.data.output, tebEl.id);
        }
      })
      .catch(function (error) {
        console.error(error);
      });
  }
  localStorage.setItem("notes", JSON.stringify(notes.value));
};
onUnmounted(() => {
  if (hammer.value) {
    hammer.value.off("swipeleft swiperight");
  }
});
// eslint-disable-next-line no-unused-vars
const addTodo = (tabId) => {
  if (newTodo.value[tabId]) {
    const todo = {
      id: uuidv4(),
      text: newTodo.value[tabId],
    };
    if (!todos.value[tabId]) {
      todos.value[tabId] = [];
    }
    todos.value[tabId].push(todo);
    newTodo.value[tabId] = "";
  }
  setTodos();
};
// eslint-disable-next-line no-unused-vars
const removeTodo = (tabId, todoIndex) => {
  const todoId = todos.value[tabId][todoIndex].id;
  const selectedTodoIndex = selectedTodos.value.indexOf(todoId);
  if (selectedTodoIndex !== -1) {
    selectedTodos.value.splice(selectedTodoIndex, 1);
  }
  todos.value[tabId].splice(todoIndex, 1);
  setTodos();
};
// eslint-disable-next-line no-unused-vars
const toggleTodoSelected = (todoId) => {
  const index = selectedTodos.value.indexOf(todoId);
  if (index === -1) {
    selectedTodos.value.push(todoId);
  } else {
    selectedTodos.value.splice(index, 1);
  }
  setTodos();
};
// eslint-disable-next-line no-unused-vars
const isSelected = (todoId) => {
  return selectedTodos.value.includes(todoId);
};
// eslint-disable-next-line no-unused-vars
// const sortedTodos = computed(() => {
//   const sorted = ref({});
//   for (const tabId in todos.value) {
//     const todoList = todos.value[tabId];
//     const selectedTodosInList = todoList?.filter(
//       (todo) => todo.id && isSelected(todo.id)
//     );
//     const unselectedTodosInList = todoList?.filter(
//       (todo) => !isSelected(todo.id)
//     );
//     sorted.value[tabId] = [...unselectedTodosInList, ...selectedTodosInList];
//   }
//   return sorted.value;
// });
</script>


<style>
body {
  background-color: #2c3e50;
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

.tabs {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}

.tabs__button {
  padding: 10px;
  border: none;
  background-color: #34495e;
  color: #ecf0f1;
  cursor: pointer;
}

.tabs__button--active {
  background-color: #27ae60;
}

.content__input {
  width: 95%;
  margin: 0 auto;
  margin-bottom: 10px;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #34495e;
  color: #ecf0f1;
}

.is-completed {
  color: #27ae60;
  transition: color 0.3s ease;
}

.content__list-item {
  display: flex;
  align-items: center;
}

.checkbox-wrapper {
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
}

.checkbox-wrapper__input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.checkbox-wrapper__checkmark {
  position: relative;
  width: 25px;
  height: 25px;
  border: 1px solid #ecf0f1;
  border-radius: 3px;
  margin-right: 10px;
}

.checkbox-wrapper__checkmark::after {
  content: "";
  position: absolute;
  display: none;
}

.checkbox-wrapper__input:checked ~ .checkbox-wrapper__checkmark::after {
  display: block;
}
.checkbox-wrapper__text.is-completed {
  /* Zmieniono tutaj */
  color: #27ae60;
}

.checkbox-wrapper__input:checked ~ .checkbox-wrapper__checkmark::after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 12px;
  border: solid #27ae60;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

.checkbox-wrapper__text {
  transition: color 0.3s ease;
  color: #bdc3c7; /* Dodaj to */
}

.content__delete-button {
  margin-left: 10px;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  background-color: #c0392b;
  color: #ecf0f1;
  cursor: pointer;
}
</style>