<template>
  <div class="hello">
    <div style="width: 400px; margin: auto; text-align: center">
      <button class="button-29" role="button" @click="startRecord">
        Start Recording
      </button>
      <button
        class="button-29"
        :class="recording ? 'button-29--stop' : ''"
        role="button"
        id="stop-button"
        @click="stopRecord"
      >
        Stop record
      </button>
      <br />
    </div>
    <div style="color: red">{{ response }}</div>
  </div>
</template>

<script>
export default {
  props: {
    text: String,
    index: Number,
    total: Number,
  },
  data() {
    return {
      recording: false,
      url: null,
      mediaRecorder: null,
      audioChunks: [],
      response: "",
    };
  },
  computed: {
    file_name() {
      let padded_number = "";
      if (this.index < 10) {
        padded_number = `00${this.index + 1}`;
      } else if (this.index < 100) {
        padded_number = `0${this.index + 1}`;
      } else {
        padded_number = `${this.index + 1}`;
      }

      return `utterance-${padded_number}.mp3`;
    },
  },
  mounted() {
    const urlParams = new URLSearchParams(window.location.search);
    const shouldRecord = urlParams.get("record") === "true";

    if (shouldRecord) {
      this.startRecord();
    }
  },
  methods: {
    startRecord() {
      this.recording = true;
      navigator.mediaDevices.getUserMedia({ audio: true }).then((stream) => {
        this.mediaRecorder = new MediaRecorder(stream);
        this.mediaRecorder.start(1000);
        const self = this;
        this.mediaRecorder.ondataavailable = function (event) {
          self.audioChunks.push(event.data);
        };
      });

      // Clear previous recording data
      this.audioChunks = [];
      this.url = null;

      // Add CSS class to change button color
      const stopButton = document.getElementById("stop-button");
      stopButton.classList.add("recording");
    },
    stopRecord() {
      this.recording = false;
      const self = this;
      this.mediaRecorder.stop();
      this.mediaRecorder.addEventListener("stop", () => {
        const audioBlob = new Blob(self.audioChunks);
        self.url = URL.createObjectURL(audioBlob);
        self.sendToWhisper(self.url);

        // Remove CSS class to revert button color
        const stopButton = document.getElementById("stop-button");
        stopButton.classList.remove("recording");

        // Close the stream
        self.mediaRecorder.stream.getTracks().forEach((track) => track.stop());
      });
    },
    async sendToWhisper(url) {
      if (url) {
        // Create a new FormData instance
        var formData = new FormData();

        // Create a blob from the audio url
        var audioBlob = await fetch(url).then((r) => r.blob());

        // Append the audio data to the form
        formData.append("file", audioBlob, `voice_${this.index}.mp3`);

        // Send the audio data to the Whisper API
        fetch("https://hook.eu1.make.com/c0hlgwfm9sfzaw4erljmmv3as63326mb", {
          method: "POST",
          body: formData,
        })
          .then((response) => response.text()) // convert to plain text
          .then((data) => {
            this.$emit("create-note", data);
            return (this.response = data.includes("Amara") ? "" : data);
          })
          .catch((error) => {
            console.error("Error:", error);
          });
      }
    },
  },
};
</script>

